import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import SvgIcon from '@material-ui/core/SvgIcon';
import CardDetail from './CardDetail';
import Hidden from '@material-ui/core/Hidden';
//Arrays for the gridlists
import { Art2D, GameDesign, Model3D, Web } from "./GridListArrays";

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 500,
        height: 450,
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    },
    titleBar: {
        background:
            'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
            'rgba(0,0,0,0.8) 60%, rgba(0,0,0,0) 100%)',
    },
    icon: {
        color: 'white',
    },
}));

export default function Showcase(props) {
    const classes = useStyles();

    const showcaseArray = function () {
        if (props.skillName === '2D Art') return Art2D;
        else if (props.skillName === 'Game Development') return GameDesign;
        else if (props.skillName === '3D Art') return Model3D;
        else if (props.skillName === 'Web Development') return Web;
        //Keep adding cases
    }

    //Card Detail stuff
    const [open, setOpen] = React.useState(false);
    const [detailCard, setDetailCard] = React.useState(null);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <Grid className={props.titleLeft ? "darkRedBG" : "paperBG"} container spacing={0} direction="row" justify="center" alignItems="center" style={{ margin: '10px auto 10px' }}>
                {props.titleLeft && <Hidden smDown><Grid className="skillTitle infoCard" item md={4}> <h2 style={{ margin: 0 }}>{props.skillName}</h2><hr className='hrLight' /><p>{props.description}</p> </Grid></Hidden>}
                <Hidden mdUp>
                    <Grid className="skillTitle infoCard" item xs={12}> <h2 style={{ margin: 0 }}>{props.skillName}</h2><hr className={props.titleLeft && 'hrLight'}/><p>{props.description}</p> </Grid>
                </Hidden>
                <Grid item md={8} xs={12}>
                    <GridList cellHeight={props.cellHeight} spacing={3} cols={props.cols} style={{ maxHeight: 600, overflow: 'auto', margin: '0' }}>
                        {showcaseArray().map((tile) => (
                            <GridListTile key={tile.img} cols={tile.featured ? 2 : 1} rows={2} onClick={function () {
                                handleClickOpen();
                                setDetailCard({
                                   tile
                                });
                            }}>
                                <img src={tile.img} alt={tile.title} />
                                <GridListTileBar
                                    title={<div style={{position: 'absolute', top: '10px', margin: '0 5px'}}>{tile.title}</div>}
                                    titlePosition="top"
                                    actionIcon={
                                        tile.software.map((program, index) => (
                                            <SvgIcon className="softIcon whiteIcon" key={tile.img + index} aria-label={program}>
                                                {program}
                                            </SvgIcon>
                                        ))}
                                    actionPosition="left"
                                    className={classes.titleBar}
                                />
                            </GridListTile>
                        ))}
                    </GridList>
                </Grid>
                {!props.titleLeft && <Hidden smDown><Grid className="skillTitle infoCard" item md={4} xs={12}> <h2 style={{ margin: 0 }}>{props.skillName}</h2><hr /><p>{props.description}</p> </Grid></Hidden>}
            </Grid>
            <CardDetail open={open} handleClose={handleClose}
                item={detailCard}
            />
        </div>
    );
}